    </div>
    <p class="title">Connect to the internet</p>
    <p class="message">You're offline. Check your connection.</p>
    <button class="btn btn-retry" @click="reloadPage">Retry</button>
  </div>
</template>

<script>
export default {
  name: "OfflinePage",
  computed: {
    theme() {
      return this.$store.getters.theme;
    },
    themeClasses() {
      let classes = "header ";

      switch (this.theme) {
        case "purplish":
          classes += "purplish-theme";
          break;
        case "bluetiful":
          classes += "bluetiful-theme";
          break;
        default:
          classes += "default-theme";
          break;
      }

      return classes;
    },
  },
  methods: {
    setOnlineListener() {
      window.addEventListener("online", this.onlineListener);
    },
    onlineListener() {
      this.$store.commit("app/updateOnlineStatus", true);

      const targetPath = window.location.href.replace(
        window.location.origin,
        ""
      );
      const currentPath = this.$route.fullPath;

      if (targetPath !== currentPath) this.$router.replace(targetPath);
    },
    reloadPage() {
      location.reload();
    },
  },
  beforeMount() {
    this.setOnlineListener();
  },
  beforeUnmount() {
    window.removeEventListener("online", this.onlineListener);
  },
};
</script>

<style scoped>
.logo {
  width: 100vw;
  height: 90px;
  position: relative;
}

.logo .wrapper {
  display: grid;
  place-items: center;
}

.default-theme .logo {
  background-color: var(--color-eerie-black);
}

.purplish-theme .logo {
  background-color: var(--color-black-blue);
}

.bluetiful-theme .logo {
  background-color: var(--color-steel-blue);
}

.logo::before {
  content: "";
  position: absolute;
  top: 0;
  width: 100%;
  height: 5px;
}

.default-theme .logo::before {
  background-color: var(--color-pantone-green);
}

.purplish-theme .logo::before {
  background-color: var(--color-tickle-me-pink);
}

.bluetiful-theme .logo::before {
  background-color: var(--color-maximum-blue-green);
}

.logo .wrapper {
  position: relative;
  width: 250px;
}

.logo-image {
  position: relative;
  max-width: 100%;
  z-index: 2;
}

.logo-text {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
  text-align: center;
  z-index: 1;
}

.default-theme .logo-text {
  color: var(--color-cultured);
}

.purplish-theme .logo-text {
  color: var(--color-spanish-pink);
}

.message-container {
  padding: 10vh 1.5rem 0;
  width: 100%;
  height: calc(100vh - 90px);
}

.title {
  margin-top: 1rem;
  font-size: 1.25rem;
  font-weight: 500;
}

.message {
  margin-top: 1rem;
}

.purplish-theme.message-container {
  background-color: var(--color-russian-violet);
  color: var(--color-spanish-pink);
}

.bluetiful-theme.message-container {
  background-color: var(--color-midnight-blue);
}

.btn-retry {
  margin-top: 2rem;
  padding-left: 0.8rem;
  padding-right: 0.8rem;
}

.default-theme .btn-retry {
  outline: 1px solid var(--color-malachite);
}

.default-theme .btn-retry:hover {
  background-color: var(--color-malachite);
  color: var(--color-eerie-black);
}

.purplish-theme .btn-retry {
  outline: 1px solid var(--color-tickle-me-pink);
}

.purplish-theme .btn-retry:hover {
  background-color: var(--color-tickle-me-pink);
  color: var(--color-black-blue);
}

.bluetiful-theme .btn-retry {
  outline: 1px solid var(--color-maximum-blue-green);
}

.bluetiful-theme .btn-retry:hover {
